/*  - -   */
.hamburger-menu {
    background: none;
    border: none;
    cursor: pointer;
    padding: 10px;
}

.hamburger-menu:hover {
    color: #657BFF;
}

/*     */
.menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;
}

/*      */
.sidebar-menu ul {
    list-style: none;
    padding: 60px 20px 20px 20px;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 20px;
    height: 100vh; /*      */
    background: #283547;
    box-shadow: -2px 0 15px rgba(0, 0, 0, 0.2);
}
.sidebar-menu ul li:hover {
    cursor: pointer;
}

.sidebar-menu li {

    margin: 0;
}

.sidebar-menu a {
    color: #fff;
    text-decoration: none;
    font-size: clamp(14px, 2.5vw, 16px);
    font-family: Arial, sans-serif;
    transition: color 0.2s ease;
    display: inline-block;
}

.sidebar-menu a:hover {
    color: #657BFF;
}

/*   (X) */
.close-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    background: none;
    border: none;
    color: #fff;
    font-size: 28px;
    cursor: pointer;
    z-index: 1002;
    padding: 0;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: color 0.2s ease;
}

.close-btn:hover {
    color: #657BFF;
}

/*     */
.animate__fadeIn {
    animation-duration: 0.3s;
}

.animate__fadeOut {
    animation-duration: 0.3s;
    animation-fill-mode: forwards;
    opacity: 0;
}