/*  */
@font-face {
  font-family: 'Philosopher-Regular';
  src: url('../public/assets/fonts/Philosopher-Regular.ttf');
}
@font-face {
  font-family: 'Philosopher-Bold';
  src: url('../public/assets/fonts/Philosopher-Bold.ttf');
}

.philosopher-regular {
  font-family: 'Philosopher-Regular';
}
.philosopher-bold {
  font-family: 'Philosopher-Bold';
}

/*  */
:root {
  --dark-bg: #111827;
  --card-bg: #1f253d;
  --accent-color: #4a7dff;
  --text-color: #ffffff;
  --subtle-text-color: #9999aa;
  --color-dark-bg: #111827; /*     */
  --color-card-bg: rgba(36, 36, 48, 0.7); /*      */
  --color-input-bg: #29293a; /*    */
  --color-text-light: #ffffff;
  --color-text-muted: #999999;
  --color-primary-button: #7983ff; /* -   */
  --color-heart-line: #e35a39; /*  Heart Line */
  --color-life: #ff5722;
  --color-health: #4caf50;
  --color-wisdom: #03a9f4;
  --color-career: #9c27b0;
}

/*   */

body {
  background-color: var(--dark-bg);
  color: var(--text-color);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.App {
  width: 450px;
  max-width: 450px;
  padding: 0px;
  box-sizing: border-box;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.menu-pages-app{
  width: 100%;
}

/*  */
header {
  margin: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  margin-bottom: 10px;
}

.back-arrow {
  padding: 0px;
  font-size: 24px;
  font-weight: 300;
  cursor: pointer;
  color: var(--text-color);
  background: none;
  border: none;
}

.title {
  font-weight: 500;
  flex-grow: 1;
  text-align: center;
  margin: 0px;
  color: var(--text-color);
}

.progress-text {
  color: var(--subtle-text-color);
}

/* - */
.progress-bar-container {
  height: 4px;
  background-color: var(--card-bg);
  border-radius: 2px;
  overflow: hidden;
  margin-bottom: 40px;
  width: 100%;
}

.progress-bar-fill {
  height: 100%;
  background-color: var(--accent-color);
  border-radius: 2px;
  width: var(--progress-width, 0%);
  transition: width 0.6s ease-in-out;
}

/*   */
main {
  text-align: center;
  padding: 0 10px;
  flex-grow: 1;
}

.sub-heading {
  font-size: 16px;
  color: var(--subtle-text-color);
  margin-bottom: 40px;
  font-weight: 300;
}

.sub-title {
  color: var(--subtle-text-color);
  margin-top: 0px;
  font-size: 12px;
  font-weight: normal;
}

/*  */
.font-size-24 {
  font-size: 24px;
}
.font-size-32 {
  font-size: 32px;
}
.mt-16 {
  margin-top: 16px;
}
.mt-32 {
  margin-top: 32px;
}
.mt-48 {
  margin-top: 32px;
}
.mt-64 {
  margin-top: 64px;
}
.pd-0{
  padding:0px;
}
.no-margin {
  margin: 0;
}

/*  */
.message-text {
  font-family: 'Open Sans', sans-serif;
  font-weight: 500;
  font-size: 18px;
  margin: 0px 13px;
  padding: 20px;
  background-color: white;
  border-radius: 25px;
  position: relative;
  color: black;
}

.blue-text {
  color: #5671ff;
}

.message-text::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid white;
}

.message-avatar {
  margin-top: 16px;
  width: 32px;
}

/*  */
.astromap-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 400px;
  position: relative;
}

.astromap-bg, .astromap, .background-pulse {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.astromap-bg {
  z-index: 1;
  animation: pulse 2s infinite, rotate 10s linear infinite;
}

.astromap {
  z-index: 3;
}

.background-pulse {
  z-index: 2;
  animation: pulse 5s linear infinite;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: rgba(128, 128, 128, 0.51);
  opacity: 0.3;
}

/*   */
.zodiacs {
  opacity: 0;
  display: flex;
  justify-content: space-around;
}
.zodiac-items img{
  width: 32px;
}

/*  */
.next-btn {
  color: var(--text-color);
  font-weight: bold;
  font-size: 18px;
  width: 290px;
  padding: 16px;
  border: none;
  border-radius: 12px;
  background-color: var(--accent-color);
  cursor: pointer;
  transition: background-color 0.2s;
}

.next-btn:hover {
  background-color: #2a304e;
}

.button-container {
  text-align: center;
  margin-top: auto;
  padding: 20px 0;
}

/*   */
.floating-messages-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  pointer-events: none;
  overflow: hidden;
}

.floating-message {
  position: absolute;
  top: 210px;
  left: 0;
  width: 100%;
  text-align: center;
  opacity: 0;
  animation: floatSequence 20s 1 linear;
}

.floating-text {
  font-family: 'Open Sans', sans-serif;
  font-weight: 500;
  font-size: 16px;
  padding: 10px 20px;
  background-color: rgba(112, 112, 112, 0.25);
  border-radius: 20px;
  color: white;
  display: inline-block;
  max-width: 70%;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
}

/*  */
.card-container {
  margin: 32px 24px;
}

.card-content {
  transition: .2s;
  margin-top: 8px;
  padding: 4px 16px;
  background-color: #283547;
  display: flex;
  align-items: center;
  border-radius: 10px;
  height: 64px;
}

.card-content:hover {
  cursor: pointer;
  background-color: #242f40;
}

.card-logo {
  font-size: 32px;
}

.card-text {
  margin: 8px;
}

/*    */
.card-row {
  margin-top: 12px;
  display: flex;
  justify-content: space-around;
}

.card-btn {
  color: white;
  border: none;
  border-radius: 8px;
  background-color: #283547;
  padding: 12px 10px;
  transition: all 0.3s ease;
  cursor: pointer;
  border: 1px solid #242f40;
}

.card-btn:hover {
  background-color: #242f40;
}

.card-btn.selected {
  background: none;
  border: 1px solid #2196f3;
}

.card-btn.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.card-btn-text {
  margin-left: 8px;
}

.card-btn-logo {
  font-size: 16px;
}

/*   */
.circle {
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

.bg-red { background-color: #d9534f; }
.bg-yellow { background-color: #d9bd4f; }
.bg-blue { background-color: #2196f3; }
.bg-orange { background-color: #d9994f; }
.bg-green { background-color: #5cb85c; }
.bg-violet { background-color: #7444bc; }

/*   */
.people-card {
  width: 100%;
  height: 475px;
  background-image: url('../public/assets/img/cardbackground.png');
  background-size: 100% auto;
  background-position: center 30%;
  background-repeat: no-repeat;
  border-radius: 12px;
  position: relative;
  overflow: hidden;
  animation: unfoldBackground 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}

.people-card-img-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: end;
  height: 160px;
}

.people-card-items {
  width: 180px;
}

.people-card-item-z1 {
  width: 220px;
  top: -10%;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  z-index: 1;
}

.people-card-item-z2 {
  width: 120px;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}

.card-footer {
  margin-top: 48px;
  display: flex;
  justify-content: space-around;
}

/*   */
.reliable-container {
  height: 32px;
  margin: 16px 32px;
  border-radius: 16px;
  background-color: rgba(106, 106, 106, 0.24);
  color: white;
  overflow: hidden;
  animation: containerCycle 12s ease-in-out 1;
  animation-delay: 5s;
}

.reliable-title {
  padding: 6px;
  font-size: 14px;
  font-weight: 600;
  text-align: center;
  margin: 0;
}

.reliable-item {
  display: flex;
  justify-content: space-between;
  margin: 0px 64px;
  font-size: 14px;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  animation-fill-mode: both;
}

/*   */
.progress-bar-circle-bg, .progress-bar-circle {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.progress-bar-circle-bg {
  z-index: 1;
  animation: pulse 2s infinite, rotate 10s linear infinite;
}

.progress-bar-circle {
  z-index: 2;
}

/*  */
@keyframes pulse {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.4;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.1);
    opacity: 0.7;
  }
}

@keyframes rotate {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

@keyframes floatSequence {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  5% {
    transform: translateY(0px);
    opacity: 1;
  }
  15% {
    transform: translateY(80px);
    opacity: 0;
  }
  20% {
    transform: translateY(160px);
    opacity: 0;
  }
}

@keyframes unfoldBackground {
  0% {
    background-size: 100% 60%;
    background-position: center 100%;
  }
  100% {
    background-size: 100% 100%;
    background-position: center 100%;
  }
}

@keyframes containerCycle {
  0% { height: 32px; }
  10% { height: 128px; }
  10%, 60% { height: 128px; }
  70% { height: 32px; }
  100% { height: 32px; }
}

@keyframes showItem {
  to {
    opacity: 1;
    max-height: 50px;
  }
}

@keyframes hideItem {
  to {
    opacity: 0;
    max-height: 0;
  }
}

/*    */
.floating-message:nth-child(1) { animation-delay: 0s; }
.floating-message:nth-child(2) { animation-delay: 1.1s; }
.floating-message:nth-child(3) { animation-delay: 2.2s; }
.floating-message:nth-child(4) { animation-delay: 3.3s; }

/*   */
.reliable-item:nth-child(1) {
  animation: showItem 0.5s 7s forwards, hideItem 0.5s 11s forwards;
}
.reliable-item:nth-child(2) {
  animation: showItem 0.5s 8s forwards, hideItem 0.5s 11.5s forwards;
}
.reliable-item:nth-child(3) {
  animation: showItem 0.5s 9s forwards, hideItem 0.5s 12s forwards;
}
.reliable-item:nth-child(4) {
  animation: showItem 0.5s 10s forwards, hideItem 0.5s 12.5s forwards;
}
.scan-anim-container {
  padding: 64px 0px;
  position: relative;
  width: 100%;
  height: 210px;
  perspective: 1000px;
}

.scan-anim {
  position: absolute;
  top: 26%;
  left: 5%;
  width: 350px;
  height: 210px;
  z-index: 1;
  filter: brightness(1.1) contrast(1.2);
  transform-style: preserve-3d;
}

.scan-anim-img {
  position: absolute;
  top: 0;
  left: 18%;
  width: 250px;
  height: 100%;
  z-index: 2;
  /*   */
  mix-blend-mode: lighten;
  filter:
          drop-shadow(0 0 15px rgba(0, 255, 255, 0.5))
          drop-shadow(0 0 30px rgba(100, 200, 255, 0.3));
  animation: handFloat 3s ease-in-out infinite;
}
.sub-button{
  padding: 0;
  margin: 0;
  text-align: center;
  color: var(--accent-color);

}
.upload-button-container{
  text-align: center;
}
.gender-selection {
  display: flex;
  gap: 15px; /*    */
  justify-content: center;
}

.gender-card {
  background-color: var(--card-bg);
  border-radius: 12px;
  padding: 25px 0;
  width: 100px; /*      */
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.2s, box-shadow 0.2s, transform 0.1s;
  border: 2px solid transparent;
}

.gender-card:hover {
  background-color: #2a304e;
}

/*   */
.gender-card:active {
  transform: scale(0.98);
  background-color: var(--accent-color); /*       */
}

/*   */
.gender-card.selected {
  border: 2px solid var(--accent-color);
  box-shadow: 0 0 10px rgba(74, 125, 255, 0.5);
}

.symbol {
  font-size: 38px;
  font-weight: 300;
  color: var(--accent-color);
  margin-bottom: 5px;
  line-height: 1;
}

.gender-card:active .symbol,
.gender-card.selected .symbol {
  color: var(--text-color);
}


.label {
  font-size: 14px;
  font-weight: 400;
  color: var(--text-color);
}
/*  DATE PICKER*/


.picker-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 80px;
  position: relative;
  height: 250px;
  overflow: hidden;
}

.picker-column {
  flex: 1;
  max-width: 150px;
  position: relative;
}

.picker-label {
  text-align: center;
  color: #6b7280;
  font-size: 14px;
  margin-bottom: 10px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.picker-wrapper {
  height: 200px;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  scrollbar-width: none;
  -ms-overflow-style: none;
  position: relative;
}

.picker-wrapper::-webkit-scrollbar {
  display: none;
}

.picker-option {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  scroll-snap-align: center;
  color: #6b7280;
  font-size: 18px;
  transition: all 0.3s ease;
  cursor: pointer;
  user-select: none;
  position: relative;
}

.picker-option.active {
  color: white;
  font-size: 24px;
  font-weight: 600;
}

.picker-highlight {
  margin-top: 4px;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 50px;
  border-top: 2px solid rgba(99, 102, 241, 0.3);
  border-bottom: 2px solid rgba(99, 102, 241, 0.3);
  pointer-events: none;
  z-index: 1;
  border-radius: 8px;
}



/*     */
@keyframes pulse {
  0% {
    text-shadow: 0 0 5px rgba(255, 255, 255, 0.3);
  }
  50% {
    text-shadow: 0 0 15px rgba(255, 255, 255, 0.5);
  }
  100% {
    text-shadow: 0 0 5px rgba(255, 255, 255, 0.3);
  }
}

.picker-option.active {
  animation: pulse 2s infinite;
}
/*time picker */
.time-info {
  text-align: center;
  margin: 30px 0;
  padding: 4px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
}

.time-info p {
  margin: 5px 0;
  color: #cbd5e0;
  font-size: 14px;
}

.time-info .date-preview {
  color: #a0aec0;
  font-size: 13px;
  font-style: italic;
}
.secondary-link{
  color: var(--accent-color);
}
.secondary-link:hover{
  cursor: pointer;
  color: #3972ff;
}


/*  */
.city-input,.email-input{
  font-size: 18px;
  color: var(--text-color);
  transition: .5s;
  border-radius: 15px;
  padding: 16px 32px;
  outline:none;
  width: 70%;
  background:none;
  border: 1px solid gray;
}
.city-input:focus{
  transition: .5s;
  outline:none;
  border: 1px solid var(--accent-color);

}

.auto-upload-indicator {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 10px;
  font-size: 14px;
}
.remove-photo-btn{
  margin-top: 12px;
  padding: 8px 12px;
  color: white;
  background: var(--accent-color);
  border: none;
  border-radius: 20%;
}
.selected-photo{
  border-radius: 16px;
}
/*  */
.container {
  width: 100%;
  max-width: 420px;
  text-align: center;
}

/*   */
.report-header {
  font-size: 1rem;
  color: var(--color-text-muted);
  margin-bottom: 15px;
  font-weight: 500;
  filter: blur(3px) brightness(0.9);
}

/*   () */
.report-box {
  background-color: var(--color-card-bg);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 40px;
  /*    -     */
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.report-content-wrapper {
  display: flex;
  align-items: flex-start;
  margin-bottom: 15px;
}

/*     */
.hand-visual {
  width: 40%;
  aspect-ratio: 1 / 1;
  border-radius: 8px;
  margin-right: 15px;
  overflow: hidden;
}

.palm-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* ---  :   BLUR --- */
  /*  3px   ,    */
  filter: blur(3px) brightness(0.9);
}

/*  */
.stats-placeholder {
  width: 60%;
  text-align: left;
  filter: blur(3px) brightness(0.9);
}

.stats-placeholder h2 {
  font-size: 0.8rem;
  text-transform: uppercase;
  color: var(--color-text-muted);
  margin-bottom: 12px;
  font-weight: 700;
  filter: blur(3px) brightness(0.9);
}

.stat-item {
  margin-bottom: 10px;
}

.stat-label {
  font-size: 0.9rem;
  font-weight: 500;
  display: block;
  margin-bottom: 2px;
}

/*   */
.progress-bar-stat-container {
  background-color: #333345;
  height: 6px;
  border-radius: 3px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  border-radius: 3px;
}

.life { background-color: var(--color-life); }
.health { background-color: var(--color-health); }
.wisdom { background-color: var(--color-wisdom); }
.career { background-color: var(--color-career); }

/*   */
.report-summary {
  font-size: 0.9rem;
  color: var(--color-text-light);
  filter: blur(3px) brightness(0.9);
}

.highlight-line {
  color: var(--color-heart-line);
  font-weight: 700;
}

/*   */
.signup-section {
  text-align: center;
}

.signup-section h2 {
  font-size: 1.5rem;
  margin-bottom: 25px;
  font-weight: 700;
  line-height: 1.3;
}

/*   */
input[type="email"] {
  width: 100%;
  padding: 15px 20px;
  margin-bottom: 15px;
  border: 1px solid #3c3c50;
  border-radius: 8px;
  background-color: var(--color-input-bg);
  color: var(--color-text-light);
  font-size: 1rem;
  text-align: center;
  font-weight: 500;
}

input[type="email"][readonly] {
  cursor: default;
  opacity: 0.9;
}

/*    */
.security-info {
  display: flex;
  align-items: flex-start;
  margin-bottom: 25px;
  text-align: left;
  color: var(--color-text-muted);
  font-size: 0.85rem;
  padding: 0 5px;
}

.security-icon {
  /*       */
  display: inline-block;
  width: 18px;
  height: 18px;
  background-color: #d32f2f; /*   */
  border-radius: 3px;
  margin-right: 10px;
  position: relative;
}

.security-icon::after {
  content: '!';
  color: white;
  font-size: 14px;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.security-info p {
  flex-grow: 1;
}

/*  "Continue" */
button[type="submit"] {
  padding: 14px 40px;
  min-width: 200px;
  max-width: 300px;
  font-size: clamp(16px, 3vw, 18px);
  border-radius: 16px;
  background-color: #657BFF;
  font-weight: 700;
  color: white;
  border: none;
  cursor: pointer;

  transition: transform 0.2s ease, box-shadow 0.2s ease;
  margin-bottom: 40px;
}

button[type="submit"]:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(101, 123, 255, 0.7);
}