


/* ---    --- */
second-header {
    background-color: #1a1a1a;
    border-bottom: 1px solid #333333;
    padding: 15px 5%;
    position: sticky;
    top: 0;
    z-index: 10;
}
.nav-links a:hover{
    cursor: pointer;
}
.top-nav-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
}

.back-button {
    /*  ,     */
    width: 20px;
    height: 20px;
}

.nav-links {
    font-size: 14px;
    white-space: nowrap;
}

.nav-links a {
    color: #cccccc;
    text-decoration: none;
    margin-left: 20px;
}

.nav-links a:hover {
    color: #ffffff;
}

/* ---   --- */
.content-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 40px 20px;
}

.main-title {
    font-size: 36px;
    font-weight: bold;
    text-align: center;
    margin-top: 50px;
    margin-bottom: 5px;
}

.updated-date {
    font-size: 16px;
    text-align: center;
    color: #aaaaaa;
    margin-bottom: 40px;
}

/*   */
.policy-paragraph {
    font-size: 16px;
    margin-bottom: 25px;
    color: #cccccc;
    text-align: justify;
}

.policy-paragraph strong, .policy-paragraph a {
    color: #ffffff;
    font-weight: 600;
}

.policy-paragraph a {
    color: #4da6ff; /*   */
    text-decoration: none;
}

/*   */
.section-title {
    font-size: 20px;
    font-weight: bold;
    margin-top: 40px;
    margin-bottom: 15px;
    color: #f0f0f0;
}

/*    */
.ordered-list {
    padding-left: 20px;
    margin-bottom: 25px;
    list-style-type: decimal; /*   */
}

.ordered-list li {
    font-size: 16px;
    margin-bottom: 10px;
    color: #cccccc;
}

.sub-list {
    margin-top: 10px;
    padding-left: 20px;
    list-style-type: disc; /*  */
}

/*     */
.notice-box {
    background-color: #333333;
    padding: 15px;
    border-left: 5px solid #ffcc00; /*      */
    font-weight: bold;
    color: #ffffff;
    margin: 30px 0;
}