.email-fadeup-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100px;
    overflow: hidden;
    padding: 0px;
}

.email-item {
    display: flex;
    align-items: center;
    gap: 15px;
    border-radius: 12px;
    padding: 16px 24px;
    animation-duration: 1s;
    animation-fill-mode: both;
    min-width: 350px;
    max-width: 450px;
    transition: transform 0.3s ease;
}

/*     */
.email-avatar {
    flex-shrink: 0;
}

.avatar-circle {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background: linear-gradient(135deg, #f5f5f5, #e0e0e0);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 700;
    color: #666;
    box-shadow:
            inset 0 2px 4px rgba(255, 255, 255, 0.8),
            0 2px 6px rgba(0, 0, 0, 0.1);
    text-transform: uppercase;
    border: 2px solid #fff;
}

/*   */
.email-content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    min-width: 0; /*     */
}

.email-address {
    font-size: 16px;
    font-weight: 600;
    color: #4a7dff;
    margin-bottom: 4px;
    font-family: 'Arial';
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
